'use client';

import { useState } from 'react';

interface ApiResponse {
  success: boolean;
  data?: any;
  error?: string;
  status?: number;
}

export default function ApiInterface() {
  const [endpoint, setEndpoint] = useState('/api/firewall/rules');
  const [method, setMethod] = useState<'GET' | 'POST' | 'DELETE'>('GET');
  const [requestBody, setRequestBody] = useState('');
  const [response, setResponse] = useState<ApiResponse | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [requestHistory, setRequestHistory] = useState<Array<{
    method: string;
    endpoint: string;
    timestamp: string;
    status: number;
  }>>([]);

  const testApi = async () => {
    setIsLoading(true);
    setResponse(null);
    
    try {
      const baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001';
      const url = `${baseUrl}${endpoint}`;
      
      const options: RequestInit = {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
      };

      if (method === 'POST' && requestBody) {
        options.body = requestBody;
      }

      const startTime = Date.now();
      const apiResponse = await fetch(url, options);
      const responseTime = Date.now() - startTime;
      
      let responseData;
      try {
        responseData = await apiResponse.json();
      } catch {
        responseData = { message: 'Response could not be parsed as JSON' };
      }

      const result: ApiResponse = {
        success: apiResponse.ok,
        data: responseData,
        status: apiResponse.status,
      };

      if (!apiResponse.ok) {
        result.error = responseData.message || `HTTP ${apiResponse.status}`;
      }

      setResponse(result);
      
      // Add to request history
      setRequestHistory(prev => [{
        method,
        endpoint,
        timestamp: new Date().toLocaleTimeString(),
        status: apiResponse.status,
      }, ...prev.slice(0, 9)]); // Keep last 10 requests
      
    } catch (error) {
      setResponse({
        success: false,
        error: error instanceof Error ? error.message : 'Network error occurred',
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleMethodChange = (newMethod: 'GET' | 'POST' | 'DELETE') => {
    setMethod(newMethod);
    setResponse(null);
  };

  const clearResponse = () => {
    setResponse(null);
  };

  const getMethodColor = (methodType: string) => {
    switch (methodType) {
      case 'GET': return 'bg-green-100 text-green-800';
      case 'POST': return 'bg-blue-100 text-blue-800';
      case 'DELETE': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      <div className="bg-white rounded-lg shadow p-6">
        <h1 className="text-3xl font-bold text-gray-900 mb-4">API Interface</h1>
        <p className="text-gray-600 text-lg leading-relaxed">
          Interact with the firewall system through RESTful API endpoints for automation and integration.
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-xl font-semibold text-gray-900 mb-4">API Endpoints</h3>
          <div className="space-y-3">
            <div className="p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded">GET</span>
                <span className="font-mono text-sm">/api/firewall/rules</span>
              </div>
              <p className="text-gray-600 text-sm">Retrieve all firewall rules</p>
            </div>
            
            <div className="p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded">POST</span>
                <span className="font-mono text-sm">/api/firewall/ip</span>
              </div>
              <p className="text-gray-600 text-sm">Add new IP rule</p>
            </div>
            
            <div className="p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="bg-red-100 text-red-800 text-xs px-2 py-1 rounded">DELETE</span>
                <span className="font-mono text-sm">/api/firewall/url</span>
              </div>
              <p className="text-gray-600 text-sm">Remove URL rule</p>
            </div>

            <div className="p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="bg-purple-100 text-purple-800 text-xs px-2 py-1 rounded">GET</span>
                <span className="font-mono text-sm">/api/firewall/ports</span>
              </div>
              <p className="text-gray-600 text-sm">Get port-based rules</p>
            </div>

            <div className="p-3 bg-gray-50 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="bg-yellow-100 text-yellow-800 text-xs px-2 py-1 rounded">GET</span>
                <span className="font-mono text-sm">/api/firewall/logs</span>
              </div>
              <p className="text-gray-600 text-sm">Retrieve firewall logs</p>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-xl font-semibold text-gray-900 mb-4">API Status</h3>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <span className="text-gray-600">API Version</span>
              <span className="font-semibold">v1.0.0</span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-gray-600">Status</span>
              <span className="text-green-600 font-semibold">Online</span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-gray-600">Response Time</span>
              <span className="font-semibold">~45ms</span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-gray-600">Requests Today</span>
              <span className="font-semibold">{requestHistory.length}</span>
            </div>
          </div>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow p-6">
        <h2 className="text-2xl font-semibold text-gray-900 mb-4">Test API</h2>
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Endpoint</label>
            <input 
              type="text" 
              value={endpoint}
              onChange={(e) => setEndpoint(e.target.value)}
              className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="/api/firewall/rules"
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">HTTP Method</label>
            <div className="flex space-x-2">
              {(['GET', 'POST', 'DELETE'] as const).map((methodType) => (
                <button
                  key={methodType}
                  onClick={() => handleMethodChange(methodType)}
                  className={`px-4 py-2 rounded-lg transition-colors ${
                    method === methodType
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                  }`}
                >
                  {methodType}
                </button>
              ))}
            </div>
          </div>

          {method === 'POST' && (
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Request Body (JSON)</label>
              <textarea
                value={requestBody}
                onChange={(e) => setRequestBody(e.target.value)}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                rows={4}
                placeholder='{"ip": "192.168.1.100", "action": "block"}'
              />
            </div>
          )}

          <div className="flex space-x-4">
            <button 
              onClick={testApi}
              disabled={isLoading}
              className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isLoading ? 'Testing...' : 'Test API'}
            </button>
            {response && (
              <button 
                onClick={clearResponse}
                className="bg-gray-500 text-white px-4 py-2 rounded-lg hover:bg-gray-600 transition-colors"
              >
                Clear Response
              </button>
            )}
          </div>
        </div>
      </div>

      {response && (
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-xl font-semibold text-gray-900 mb-4">Response</h3>
          <div className={`p-4 rounded-lg ${
            response.success ? 'bg-green-50 border border-green-200' : 'bg-red-50 border border-red-200'
          }`}>
            <div className="flex items-center space-x-2 mb-3">
              <span className={`px-2 py-1 rounded text-sm font-medium ${
                response.success ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
              }`}>
                {response.success ? 'Success' : 'Error'}
              </span>
              {response.status && (
                <span className="text-sm text-gray-600">Status: {response.status}</span>
              )}
            </div>
            
            {response.error && (
              <div className="mb-3">
                <span className="font-medium text-red-800">Error: </span>
                <span className="text-red-700">{response.error}</span>
              </div>
            )}
            
            {response.data && (
              <div>
                <span className="font-medium text-gray-800">Data: </span>
                <pre className="mt-2 p-3 bg-gray-100 rounded-lg overflow-x-auto text-sm">
                  {JSON.stringify(response.data, null, 2)}
                </pre>
              </div>
            )}
          </div>
        </div>
      )}

      {requestHistory.length > 0 && (
        <div className="bg-white rounded-lg shadow p-6">
          <h3 className="text-xl font-semibold text-gray-900 mb-4">Recent Requests</h3>
          <div className="space-y-2">
            {requestHistory.map((request, index) => (
              <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-3">
                  <span className={`px-2 py-1 rounded text-xs font-medium ${getMethodColor(request.method)}`}>
                    {request.method}
                  </span>
                  <span className="font-mono text-sm text-gray-700">{request.endpoint}</span>
                </div>
                <div className="flex items-center space-x-3 text-sm text-gray-500">
                  <span>{request.timestamp}</span>
                  <span className={`px-2 py-1 rounded text-xs ${
                    request.status >= 200 && request.status < 300 
                      ? 'bg-green-100 text-green-800' 
                      : 'bg-red-100 text-red-800'
                  }`}>
                    {request.status}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
